import {createStore, combineReducers} from 'redux';
import obj from '../data'
import { stat } from 'fs';



function maxReducer(state=obj, action) {
    switch(action.type){
        case 'GET-DATA':

            return {
                staticData: state.staticData,
                data:state.staticData,
                selectedValue : 'All',
 
                
            };
        case 'FILTER-DATA':
            let filteredItems = state.staticData.filter(el=>{
               return el.gender === action.gender && el.type === action.typeClothes
            })
            return  {
                staticData: state.staticData,
                data:filteredItems,
                selectedValue : 'All',
            };
        case 'FILTER-DATA-BRAND':
            if ( action.brandVar === 'All') {
                return {
                    staticData: state.staticData,
                    data:state.data,
                    selectedValue : action.brandVar,
                }
            } else {
                let filterData = state.data.filter(el=>{
                    return el.brand === action.brandVar
                 });
                 return  {
                     staticData: state.staticData,
                     data:state.data,
                     brandData: filterData,
                    selectedValue : action.brandVar,
                 };  

            };
            case 'FILTER-DATA-PRICE':
                    let filterData;
            if (state.brand) {
                 filterData = (state.brandData?state.brandData : state.data).filter(el=>{
                    return el.price >= action.minPrice && el.price <= action.maxPrice
                 });
            } else if (state.data) {
                filterData = (state.brandData?state.brandData : state.data).filter(el=>{
                    return el.price >= action.minPrice && el.price <= action.maxPrice
                 });
            }
            return {
                ...state,
                priceFilteredData: filterData,
                minPrice:action.minPrice ,
                maxPrice : action.maxPrice,
               
            }       
            case 'ADD-TO-BASKET':
            // console.log(action.id)
            // console.log(state)

            const newData = state.data.map((el) => el.id !== action.id ? el : ({...el, isAdded: true}));
            
            const newState = {
                ...state,
                data: newData
            }

            /*
            for (var key in state) {
                if (key !== 'selectedValue' && key !== 'staticData' ) {
                   state[key].forEach(el=>{
                   if (el.id === action.id) {
                    return   el.isAdded = true;
                   }
                   

                 })       
                 
                }
            
            }
            console.log(state)
            */
            console.log(newState);
            return newState;


        default:
            return state
    }

  }

  function reducer(state, action) {
      const newState = maxReducer(...arguments);
      return {
          ...newState,
          data: newState.data.map(el => ({...el, img: el.img.replace(/^\//, "")}))
      };
  }
  const store = createStore(reducer,  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())
  
  store.dispatch({type:'GET-DATA'})
 
  export default store










