import React, { Component } from 'react';
import Pagination from "react-pagination-js";
import "react-pagination-js/dist/styles.css"; 
import { NavLink  } from 'react-router-dom';
import { connect } from 'react-redux';

class ProductList extends Component {
    state = {
        currentPage: 1,
        totalSize:Math.ceil(this.props.obj.length/10)*10,
        data: []
    
      };

      static getDerivedStateFromProps(props, state) {
        const prevData = state.data;
        const newData = props.obj;


        if(newData === prevData) {
          return {};
        }

        // console.log("Data has changed")
          
        const stateUpdate = {
          totalSize: newData ? newData.length : 0,
          data: newData ? newData : [],
          currentPage: 1
        }

        return stateUpdate;
      }
    
      changeCurrentPage = numPage => {
        this.setState({ currentPage: numPage });
      };
    
    basketHandler=(id)=>{
        this.props.addToBasket(id)
    }
    
      render() {
        const { data, currentPage, totalSize } = this.state;

        return (
            <>
          <div className="product-container"> 
                {data.slice(currentPage*12-12,currentPage*12).map(el=>{
                      let slug = el.id
                      
                            return (
                                <ul className="product-item" key={el.id}>
                                    <li>For {el.gender}</li>
                                    <img src={el.img} alt="clothes" /> 
                                    <li>{el.type}</li>
                                    <li>Price {el.price} $</li>
                                    <li>Brand {el.brand}</li>
                                    <li><button onClick={()=>this.basketHandler(el.id)} disabled={el.isAdded}>{el.isAdded?  ('Added to basket') : ('Add to basket') }</button></li>
                                    <li><NavLink  to={`/item/${slug}`}  className="nav-link" >More info ... </NavLink ></li>
                                </ul>
                            )
                 })}
          </div>
          <div className="paginator">

            {this.props.obj.length > 12 ?  ( 
              <Pagination   currentPage={currentPage}
              totalSize={totalSize} 
              sizePerPage={10} 
              changeCurrentPage={this.changeCurrentPage} 
              firstPageText="first" 
              lastPageText="last" 
              showFirstLastPages={true} 
              nextPageText="next"
              previousPageText="prev"
              />
            ) : null}

            </div>
              </>
        );
      }
}

export default connect(state=>({

}), 
 dispatch=>({
     addToBasket:(id)=>{
      dispatch({type:'ADD-TO-BASKET',id})
     },

 })

)(ProductList)