import React, { Component } from 'react';
import { connect } from 'react-redux';
import ProductList from './ProductList';
import  { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

 class Products extends Component {
    state ={
        selected:true,
        value4: {
            min: 5,
            max: 10,
          }
    }
    brandHandler=(e)=>{

        // console.log(e.target.value)
        let brandVar = e.target.value;
        this.props.filterFunction(brandVar)
    }
    handleOnChange(e){
        console.log(e)
        let minPrice = e[0];
        let maxPrice = e[1];
        this.props.filterByPrice(minPrice,maxPrice)
    }


    render() {
        let brandArr = []
        this.props.data.data.forEach(el=>{
            brandArr.push(el.brand)
        })

        brandArr=['All',...new Set(brandArr)];

        let priceArr =[];
        (this.props.data.brandData? this.props.data.brandData : this.props.data.data).forEach(el=>{
            priceArr.push(el.price)
        })

        let minPrice = Math.min(...priceArr);
        let maxPrice= Math.max(...priceArr);
    
        return (
            <div className="search-and-products-container">
            <label htmlFor="filter-by-brand">Choose your brand : </label>
            <select value={this.props.data.selectedValue}   onChange={(e)=>this.brandHandler(e)} id="filter-by-brand">
                {brandArr.map((el)=>{
                    return <option value={el}  key={el}>{el}</option>
                })}
            </select>
            <div className="price-range">
                <h3  className="range-title">Min price : {this.props.data.minPrice?this.props.data.minPrice: minPrice} $ </h3>
                <h3  className="range-title">Max price : {this.props.data.maxPrice?this.props.data.maxPrice: maxPrice}  $</h3>
             <Range key={minPrice} min={minPrice}   max={maxPrice} defaultValue={[minPrice,maxPrice]} step={1} allowCross={false}  onChange={(e)=>this.handleOnChange(e)}/>
            </div>
            <div >
                <ProductList  obj={this.props.data.priceFilteredData? this.props.data.priceFilteredData: this.props.data.brandData? this.props.data.brandData : this.props.data.data }/>
            </div>
            </div>
        )
    }
}


export default connect(state=>({
    data:state
}), 
   dispatch=>({
       filterFunction:(brandVar)=>{
        dispatch({type:'FILTER-DATA-BRAND',brandVar})
       }, 
       filterByPrice:(minPrice,maxPrice)=>{
        dispatch({type:'FILTER-DATA-PRICE',minPrice,maxPrice})
       }

   })
)(Products)